@font-face {
  font-family: "PT Mono";
  font-weight: 400;
  font-style: normal;
  src: url("./../fonts/pt-mono-regular.woff2") format("woff2"), url("./../fonts/pt-mono-regular.woff") format("woff"); }

@font-face {
  font-family: "Roboto Condensed";
  font-weight: 400;
  font-style: normal;
  src: url("./../fonts/roboto-condensed-regular.woff2") format("woff2"), url("./../fonts/roboto-condensed-regular.woff") format("woff"); }

@font-face {
  font-family: "Roboto Condensed";
  font-weight: 700;
  font-style: normal;
  src: url("./../fonts/roboto-condensed-bold.woff2") format("woff2"), url("./../fonts/roboto-condensed-bold.woff") format("woff"); }

html,
body {
  margin: 0;
  padding: 0;
  height: 100%;
  width: 100%; }

html {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  scroll-behavior: smooth; }

body {
  font-family: "PT Mono", "Courier New", monospace;
  font-size: 16px;
  font-weight: 400; }

*,
*::after,
*::before {
  -webkit-box-sizing: inherit;
          box-sizing: inherit; }

h2 {
  font-family: "Roboto Condensed", "Courier New", monospace;
  font-weight: 400;
  font-size: 36px;
  line-height: 51px; }
  @media (max-width: 102px) {
    h2 {
      font-size: 32px;
      line-height: 140.62%; } }
  @media (max-width: 767px) {
    h2 {
      font-size: 28px; } }

img {
  display: block;
  max-width: 100%;
  height: auto; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
          clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.page-header {
  background-color: #1b2d37; }

.page-header button {
  padding: 14px 26px 13px;
  font-family: "Roboto Condensed", "Courier New", monospace;
  font-weight: 700;
  font-size: 14px;
  line-height: 16px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: #ffffff;
  background-color: inherit;
  border: 1px solid #ffffff;
  border-radius: 5px; }

.contacts-list {
  padding: 0;
  margin: 0;
  list-style: none; }

.contacts-list a {
  line-height: 18px;
  color: #ffffff;
  text-decoration: none; }

.container {
  max-width: 1490px;
  margin-left: auto;
  margin-right: auto; }

.promo {
  background-color: #1b2d37; }

.promo h1 {
  margin-top: 0;
  margin-bottom: 0;
  font-family: "Roboto Condensed", "Courier New", monospace;
  font-weight: 700;
  font-size: 80px;
  line-height: 94px;
  letter-spacing: 0.07em;
  text-transform: uppercase;
  color: #ffffff; }
  @media (max-width: 102px) {
    .promo h1 {
      font-size: 60px;
      line-height: 70px; } }
  @media (max-width: 767px) {
    .promo h1 {
      font-size: 40px;
      line-height: 47px; } }

.promo p {
  margin-top: 0;
  margin-bottom: 0;
  font-size: 22px;
  line-height: 34px;
  color: #ffffff; }
  @media (max-width: 102px) {
    .promo p {
      font-size: 18px;
      line-height: 156.8%; } }
  @media (max-width: 767px) {
    .promo p {
      font-size: 16px; } }

.promo a {
  display: block;
  padding: 17px 28px 14px;
  font-weight: 700;
  line-height: 19px;
  letter-spacing: 0.05em;
  color: #ffffff;
  text-decoration: none;
  text-transform: uppercase;
  background-color: #00b2ff;
  border-radius: 5px; }

.advantage ul {
  padding: 0;
  margin: 0;
  list-style: none; }

.advantage li {
  background-color: #ffffff;
  border: 1px solid #ebebeb; }

.advantage h3 {
  margin: 0;
  font-family: "Roboto Condensed", "Courier New", monospace;
  font-weight: 400;
  font-size: 24px;
  line-height: 34px;
  color: #00b2ff; }
  @media (max-width: 767px) {
    .advantage h3 {
      font-size: 20px;
      line-height: 140.62%; } }

.advantage p {
  margin: 0;
  line-height: 22px;
  color: #666666; }

.about-company h2 {
  margin: 0;
  color: #666666; }

.about-company p {
  margin: 0;
  line-height: 22px;
  color: #666666; }

.about-company a {
  display: inline-block;
  padding: 15px 50px 12px;
  font-family: "Roboto Condensed", "Courier New", monospace;
  font-weight: 700;
  font-size: 14px;
  line-height: 16px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: #00b2ff;
  text-decoration: none;
  border: 1px solid #00b2ff; }

.services {
  background-color: #1b2d37; }

.services h2 {
  margin: 0;
  color: #ffffff; }

.services ul {
  padding: 0;
  margin: 0;
  list-style: none; }

.services a {
  font-family: "Roboto Condensed", "Courier New", monospace;
  font-weight: 400;
  font-size: 20px;
  line-height: 30px;
  color: #ffffff;
  text-decoration: none; }

.user-data {
  background-color: #00b2ff; }

.user-data h2 {
  margin: 0;
  color: #ffffff; }

.user-data p {
  margin: 0;
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
  color: #ffffff; }

.user-data input,
.user-data textarea {
  padding: 0;
  font-family: "PT Mono", "Courier New", monospace;
  font-size: 16px;
  line-height: 22px; }

.user-data button {
  padding: 14px 79px 13px;
  font-family: "Roboto Condensed", "Courier New", monospace;
  font-weight: 700;
  font-size: 14px;
  line-height: 16px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: #ffffff;
  background-color: inherit;
  border: 1px solid #ffffff;
  border-radius: 5px; }

.user-data__checkbox label {
  font-size: 14px;
  line-height: 16px;
  color: rgba(255, 255, 255, 0.8); }

.page-footer {
  background-color: #fafafa; }

.social-list {
  padding: 0;
  margin: 0;
  list-style: none; }

.sections ul {
  padding: 0;
  margin: 0;
  list-style: none; }

.sections a {
  font-size: 15px;
  line-height: 25px;
  text-decoration: none;
  color: rgba(27, 45, 55, 0.5); }

.our-office ul {
  padding: 0;
  margin: 0;
  list-style: none; }

.our-office a {
  font-size: 15px;
  line-height: 25px;
  color: rgba(27, 45, 55, 0.5);
  text-decoration: none; }

.special-list {
  margin: 0;
  padding: 0;
  list-style: none; }

.special-list a {
  font-size: 13px;
  line-height: 24px;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  text-decoration: none;
  color: rgba(27, 45, 55, 0.5); }
